$p1-color: #d1453b;
$p2-color: #eb8909;
$p3-color: #246fe0;
$p4-color: #666;

.task {
  position: relative;
  &:hover .taskTools {
    display: flex;
    align-items: center;
  }
}

.taskContent {
  position: relative;
  font-size: 16px;
  padding: 8px;
  display: flex;
  height: 60px;
  justify-content: space-between;
}

.taskTitle {
  font-size: 20px;
}
.taskDescription {
  font-size: 10px;
}

.taskTools {
  display: none;
}

.moreButton {
  margin-left: 12px;
  background-color: transparent;
  border: 0;
  cursor: pointer;
  position: relative;
}

.trashButton {
  display: flex;
  align-items: center;
  background-color: transparent;
  border: 0;
  cursor: pointer;
  position: relative;
  font-size: 14px;
  &:hover {
    color: #605c5c;
  }
}

.editButton {
  display: flex;
  align-items: center;
  background-color: transparent;
  border: 0;
  cursor: pointer;
  position: relative;
  font-size: 14px;
  &:hover {
    color: #605c5c;
  }
}

.moreIcon {
  font-size: 24px;
  &:hover {
    color: #605c5c;
  }
}

.editIcon {
  font-size: 24px;
  &:hover {
    color: #605c5c;
  }
}

.trashIcon {
  font-size: 16px;
  margin-right: 5px;
}

.taskLeft {
  display: flex;
}

.moreContainer {
  position: relative;
}
